<template>
<modal-report v-model:visible="modalFeedbackVisible"/>
<modal-result v-model:visible="modalResultVisible" :percentage='lessonPercentage' :percentageAvg='lessonAvgPercentage'/>

<main-layout :backgroundImage="backgroundURL" backgroundOffset="10">

  <template #header>
    <div class="header-section">
      <div class="lesson-navbar">
        <router-link to="lessons" class="link">
          <span class="material-icons">arrow_back</span>&nbsp;
          Назад до списку тем
        </router-link>
        <span class="link" @click="modalFeedbackVisible = true">Помітили помилку?</span>
      </div>
      <div class="lesson-name">{{ lessonName }}</div>
      <div><pagination v-model:page='taskIndex' :pageMax='lesson.length' :pageLimit='9' :colors='paginationColors' ref='pagination'/></div>
    </div>
  </template>

  <template #body>
    <task-content :taskIndex='taskIndex' @answer="postAnswer" @skip="skipAnswer">
      <button id='button-back' class='button-plain button-next' :disabled="taskIndex == 0" @click='taskGoto(taskIndex-1)'>Назад</button>
      <button class='button-plain button-next' v-if="taskIndex !== lesson.length-1" @click='taskGoto(taskIndex+1)'>Далі</button>
      <button class='button-plain' v-else :disabled="!lessonCompleted" @click='modalResultVisible = true'>Результат</button>
    </task-content>
  </template>

</main-layout>
</template>

<script>
import controller from '@/api/controller'
import mainLayout from '@/layouts/main-layout.vue'
import taskContent from '@/components/task-content.vue'
import pagination from '@/components/pagination.vue'
import modalResult from '@/components/modals/modal-result.vue'
import modalReport from '@/components/modals/modal-report.vue'
export default {
  components: { mainLayout, taskContent, pagination, modalResult, modalReport },
  beforeMount() { 
    this.$nextTick(_ => MathJax.Hub.Queue(["Typeset", MathJax.Hub]))
  },
  data() { return {
    taskIndex: 0,
    showResult: false,
    modalConfirmationVisible: false,
    modalResultVisible: false,
    modalFeedbackVisible: false,
    moduleName: localStorage.getItem('moduleName'),
    lessonName: localStorage.getItem('lessonName'),
    blockName: localStorage.getItem('blockName'),
    moduleId: localStorage.getItem('moduleId'),
    lessonId: parseInt(localStorage.getItem('lessonId')),
    blockId: parseInt(localStorage.getItem('blockId')),
    colorTheme: localStorage.getItem('colorTheme'),
  }},
  computed: {
    lesson() { return this.$store.state.tasks },
    lessonCompleted() { return !this.lesson.map(e => e.completed).includes(false) },
    lessonPercentage() { return Math.round(this.lesson.map(e => e.score).sum() / this.lesson.map(e => e.weight).sum() * 100) },
    lessonAvgPercentage() { return Math.round(this.lesson.map(e => e.type=='radio' ? e.correctAnswersTotal / (e.answersTotal || 1) * 100 : 0).average()) },
    paginationColors() { return this.lesson.map(e => e.completed ? this.mix3(e.percentage) : 'gray') },
    backgroundURL() { return this.$cloudinary + this.moduleId + '.jpg' },
  },
  methods: {
    postAnswer(taskData) { controller.postAnswer(this.moduleId, this.blockId, this.lessonId, taskData) },
    skipAnswer(taskId) { controller.skipAnswer(taskId) },
    taskGoto(val) { this.$refs.pagination.pageGoto(val) },
    reportMistake() { controller.reportMistake() }
  }
}
</script>

<style lang="scss" scoped>
//* {border: 1px solid red}
$mobile: 639px;

.input-textfield-line {
  min-width: 100%;
  max-width: 100%;
  margin-top: .5em;
}

.header-section {
  width: min(75em,100%);
  margin: auto;
  padding-bottom: 1em;
  .lesson-navbar {
    display: flex;
    align-items: center;
    margin-bottom: 12vmin;
    font-size: .9em;
    color: #fff;
    *:last-child { margin-left: auto }
  }
  .lesson-name {
    display: inline-block;
    margin-bottom: 6vmin;
    font-size: clamp(1.5em, 4.2vmin, 2em);
    font-weight: bold;
    text-transform: uppercase;
    line-height: 1.5em;
    color: #fff;
  }
}

button { @media (max-width: $mobile) { flex-basis: 100%; } }
#button-back { @media (max-width: $mobile) { display: none } }
</style>